'use client';
import React, { useState } from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Search } from 'lucide-react';

interface SearchResult {
  id: string;
  title: string;
  snippet: string;
  link: string;
}

const SearchPage: React.FC = () => {
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [results, setResults] = useState<SearchResult[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string>('');

  const handleSearch = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      const res = await fetch(`/api/search?query=${encodeURIComponent(searchTerm)}`);
      const data = await res.json();

      if (data.success) {
        setResults(data.results);
      } else {
        setError('No results found.');
      }
    } catch (err) {
      setError('An error occurred while fetching data.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="bg-white shadow-sm">
        <div className="container mx-auto px-6 py-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">CrimeDB - The Criminal Database</h1>
              <p className="mt-2 text-gray-600">Secure. Efficient. Reliable.</p>
            </div>
            <Search className="h-12 w-12 text-blue-600" />
          </div>
        </div>
      </div>

      <div className="container mx-auto px-6 py-8">
        <form onSubmit={handleSearch} className="flex w-full max-w-xl mb-6">
          <input
            type="text"
            placeholder="Search criminal records..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="flex-grow border border-gray-300 rounded-l-md p-4 shadow-md focus:outline-none focus:ring focus:ring-blue-500"
          />
          <Button type="submit" className="bg-blue-600 text-white rounded-r-md p-4 hover:bg-blue-700 transition duration-200">
            Search
          </Button>
        </form>

        {loading && <p className="mt-4 text-gray-600">Loading...</p>}
        
        {error && <p className="mt-4 text-red-500">{error}</p>}

        <div className="mt-6 grid grid-cols-1 gap-6 md:grid-cols-2 lg:grid-cols-3">
          {results.map((result) => (
            <Card key={result.id} className="bg-white shadow-lg hover:shadow-xl transition-shadow duration-300 rounded-lg">
              <CardContent className="p-4">
                <a href={result.link} target="_blank" rel="noopener noreferrer" className="text-blue-600 hover:underline">
                  <h2 className="text-xl font-semibold">{result.title}</h2>
                </a>
                <p className="text-gray-700">{result.snippet}</p>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </div>
  );
};

export default SearchPage;
